.header {
  position: relative;
  background-color: $button-text;

  @media (min-width: $desktop-width) {
    padding: 47px 60px 53px 60px;
  }
}

.header__wrapper {
  display: none;

  @media (min-width: $desktop-width) {
    display: flex;
    padding: 0;
    justify-content: flex-start;
    width: 950px;
  }

  &--js {
    position: relative;
    display: flex;
    padding: 27px 25px 0;
    align-items: center;

    @media (min-width: $tablet-width) {
      padding: 45px 40px 0 40px;
    }

    @media (min-width: $desktop-width) {
      padding: 0;
    }
  }
}

.header__logo-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;

  @media (min-width: $desktop-width) {
    flex-grow: 0;
    margin-right: 145px;
  }
}

.header__site-list {
  position: relative;
  display: none;

  @media (min-width: $desktop-width) {
    display: flex;
    align-items: center;
    margin-left: 40px;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: -105px;
      display: block;
      width: 60px;
      height: 2px;
      border-radius: 1px;
      background-color: $color-white;
    }
  }
}

.header__button {
  display: inline-block;
}
